* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main-section {
  display: flex;

  .search-bar {
    height: 10vh;
    border: 1px solid black;
  }

  .song-content {
    height: 90vh;
    border: 1px solid black;

    .header {
      width: 100%;
    }
  }

  .homesongs {
    img:hover {
      filter: grayscale(50%);
    }
  }
  .searchterm {
    height: 3rem;
    border-radius: 30px 30px;
    outline: none;
    font-size: 1.5rem;
  }

  .Profile {
    display: flex;
    justify-content: center;
    align-items: center;

    .nav-item {
      list-style: none;
    }
  }
}
